<template>
  <div class="bgContent" v-lazy:background-image="imgObj" ref="container" :style="phoneType && phoneType == 'iphone6plus' ? 'height: 12rem': ''" >
    <div class="textareaClass">
      <van-field
        v-model="message"
        type="textarea"
        center
        placeholder="输入你想写在符上的话（8个字符以内）"
        rows="4"
        maxlength="8"
        @input="textareaChange"
      />
    </div>
    <div class="whiteBg">
      <p class="line"></p>
      <p style="line-height: 4px">选择符的模版</p>
      <p class="line"></p>
    </div>
    <div class="templateBg">
      <div class="divTemplate" @click="changeCheck('checked1', 'checked2', 'checked3')">
        <img v-lazy='templateUrl[3].src' alt="very cool logo" class="transparentCheck">
        <img v-lazy='templateUrl[4].src' alt="very cool logo" class="transparentCheck" :style="checked1 ? 'z-index: 110' : 'z-index: 99'">
        <img v-lazy='templateUrl[1].src' alt="very cool logo">
      </div>
      <div class="divTemplate" @click="changeCheck('checked2', 'checked1', 'checked3')">
        <img v-lazy='templateUrl[3].src' alt="very cool logo" class="transparentCheck">
        <img v-lazy='templateUrl[4].src' alt="very cool logo" class="transparentCheck"  :style="checked2 ? 'z-index: 110' : 'z-index: 99'">
        <img v-lazy='templateUrl[0].src' alt="very cool logo">
      </div>
      <div class="divTemplate" @click="changeCheck('checked3', 'checked1', 'checked2')">
        <img v-lazy='templateUrl[3].src' alt="very cool logo" class="transparentCheck">
        <img v-lazy='templateUrl[4].src' alt="very cool logo" class="transparentCheck"  :style="checked3 ? 'z-index: 110' : 'z-index: 99'">
        <img v-lazy='templateUrl[2].src' alt="very cool logo">
      </div>
    </div>
    <div class="btnDiv">
      <img v-lazy='templateUrl[5].src' alt="very cool logo" :style="btnflag ? 'z-index: 90' : 'z-index: 100'">
      <img v-lazy='templateUrl[6].src' alt="very cool logo" :style="btnflag ? 'z-index: 100' : 'z-index: 90'" @click="generateImg">
    </div>
    <!-- <div class="makediv" id="makediv">
      <img v-lazy='templateUrl[7].src' alt="very cool logo">
      <img v-lazy='templateUrl[8].src' alt="very cool logo">
      <img v-lazy='templateUrl[9].src' alt="very cool logo">
    </div> -->
  </div>
</template>
<script>
import Parallax from 'vue-parallaxy'
import { Button, Loading, Lazyload, Dialog, Toast, Field} from 'vant'
import device from '@/common/device.js'
import { mapActions } from 'vuex'
export default{
  components: {
    Parallax,
    [Button.name]:Button,
    [Loading.name]:Loading,
    [Lazyload.name]:Lazyload,
    [Dialog.name]:Dialog,
    [Toast.name]:Toast,
    [Field.name]:Field
  },
  data () {
    return {
      imgObj: {
        src: require('../../../static/images/talisman/custom/bg.png'),
        error: require('../../../static/images/error.jpg'),
        loading: require('../../../static/images/loading.gif')
      },
      templateUrl: [
        {src: require('../../../static/images/talisman/custom/pink.png')}, // 0
        {src: require('../../../static/images/talisman/custom/yellow.png')}, // 1
        {src: require('../../../static/images/talisman/custom/blue.png')}, // 2
        {src: require('../../../static/images/talisman/custom/check.png')}, // 3
        {src: require('../../../static/images/talisman/custom/checked.png')}, // 4
        {src: require('../../../static/images/talisman/custom/build.png')}, // 5
        {src: require('../../../static/images/talisman/custom/builded.png')} // 6
      ],
      message: '',
      phoneType: '',
      checked1: true, // 默认展示透明的
      checked2: false, // 默认展示透明的
      checked3: false, // 默认展示透明的
      btnflag: false // 默认展示灰色立马生成
    }
  },
  methods: {
    changeCheck (name1, name2, name3) {
      this[name1] = true
      this[name2] = false
      this[name3] = false
      if (this.message && (this.checked1 || this.checked2 || this.checked3)) {
        this.btnflag = true
      } else {
        this.btnflag = false
      }
    },
    textareaChange () {
      if (this.message && (this.checked1 || this.checked2 || this.checked3)) {
        this.btnflag = true
      } else {
        this.btnflag = false
      }
    },
    generateImg () {
      this.$store.dispatch('setText', this.message)
      let index = this.checked1 ? 0 :(this.checked2 ? 1 : (this.checked3 ? 2: null))
      console.log(index, 107)
      this.$store.dispatch('setImgIndex', index)
      this.$router.push({path: '/blessing'})
    }
  },
  mounted () {
    let width = window.screen.width
    let height = window.screen.height
    // 根据安卓和ios设备展示样式
    if (device.isAndroidDevice()) {
      this.deviceFlag = 'android'
    }
    if (device.isiOSDevice()) {
      this.deviceFlag = 'ios'
      if (width == 414 && height == 736) {
        this.phoneType = "iphone6plus"
      }
      if (width == 375 && height == 667) {
        this.phoneType = "iphone6"
      }
      if (width == 375 && height == 812) {
        this.phoneType = "iphonex"
      }
    }
    const that = this
    window.onresize = () => {
      return (() => {
        window.fullHeight = document.documentElement.clientHeight
        that.fullHeight = window.fullHeight
      })()
    }
  }
}
</script>
<style lang="less">
html,body{
  margin:0;
  height: 100%;
  padding: 0;
  overflow: hidden;
}
.bgContent[lazy=loading] {
  background-repeat: no-repeat;
  background-position:center;
}
.Masthead {
  min-height: 100% !important;
}
.bgContent{
  width: 7.5rem;
  height: 13.34rem;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.textareaClass{
  position: absolute;
  top: 0.3rem;
  left: 0.3rem;
  width: 6.9rem;
  height: 3rem;
}
.whiteBg{
  position: absolute;
  top:3rem;
  left: 0.3rem;
  width: 100%;
  height: 0.6rem !important;
  overflow: hidden;
  p {float: left; color: #fff;margin-right: 1px;margin-left: 1px;width: 30%;};
}
.line {
  padding: 0.5px;
  background-color: #fff;
  width: 30%;
}
.templateBg {
  position: absolute;
  left: 0.3rem;
  top: 4.3rem;
  height: 2rem;
  overflow: hidden;
  div {
    float: left;
    margin-left: 0.3rem;
    position: relative;
    img {
      width: 2rem;
    }
    .transparentCheck {
      width: 0.5rem;
      height: 0.5rem;
      position: absolute;
      top: 0.75rem;
      left: 0.75rem;
      z-index: 100
    }
  }
}
div.btnDiv {
  top: 10rem;
  left: 0.75rem;
  position: absolute;
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 6rem;
    height: 0.88rem;
  }
}
.makediv{
  position: absolute;
  top: 1rem;
  left: 1.25rem;
  width: 5.3rem;
  height: 8.5rem;
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 5.3rem;
    height: 8.5rem;
    display: none;
  }
}
</style>


